export function p2f(s, num = 2) {
  return parseFloat(s).toFixed(num);
}

export function normFile(e) {
  if (Array.isArray(e)) {
    return e;
  }
  return e && e.fileList && e.fileList.slice(-1);
}

export function generateUploadFileUrl(fileList) {
  if (fileList[0] && fileList[0].error !== 'error') {
    return fileList[0] && fileList[0].response && fileList[0].response.result;
  }
  return undefined;
}

const s4 = () => {
  return (((1 + Math.random()) * 0x10000) | 0).toString(16).substr(1);
};

export function generateUUID() {
  return 'id' + s4() + s4() + s4();
}

export function convertFileUrlToObj(url, name) {
  if (!url) return [];
  const file = {
    uid: generateUUID(),
    response: {
      result: url,
    },
    url: url,
    status: 'success',
    name,
  };
  return [file];
}

export const getrules = (mes) => {
  // const { rm = '' } = opt;
  return [
    {
      required: true,
      message: mes ? mes : '请录入相应信息',
    },
  ];
};
export function encode(str) {
  return window.btoa(encodeURIComponent(str));
}

export function decode(str) {
  return decodeURIComponent(window.atob(str));
}

export function downLoadBlob(data) {
  const blobUrl = window.URL.createObjectURL(data);
  const eleLink = document.createElement('a');
  eleLink.download = '数据统计分析系统';
  eleLink.style.display = 'none';
  eleLink.href = blobUrl;
  // 触发点击
  document.body.appendChild(eleLink);
  eleLink.click();
  // 然后移除
  document.body.removeChild(eleLink);
}

function _sStorage() {
  return {
    set: (key, value, obj) => {
      var value = obj ? JSON.stringify(value) : value;
      window.sessionStorage.setItem(key, value);
    },
    get: (key, obj) => {
      return obj
        ? JSON.parse(window.sessionStorage.getItem(key))
        : window.sessionStorage.getItem(key);
    },
    clear: () => {
      window.sessionStorage.clear();
    },
  };
}

function _lStorage() {
  return {
    set: (key, value, obj) => {
      var value = obj ? JSON.stringify(value) : value;
      window.localStorage.setItem(key, value);
    },
    get: (key, obj) => {
      return obj
        ? JSON.parse(window.localStorage.getItem(key))
        : window.localStorage.getItem(key);
    },
    clear: () => {
      window.localStorage.clear();
    },
  };
}

export const sStorage = _sStorage();
export const lStorage = _lStorage();
