import React, { memo, useCallback } from 'react';
import './index.styl';
import { Form, Input, Row, Col, Button, notification } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { getrules, lStorage } from '../../utils';
import { useHistory } from 'react-router-dom';
import { login } from '../../request';
import bg from '@/assets/bg.webp';

const LoginPage = (props) => {
  const [form] = Form.useForm();

  const history = useHistory();

  const onReset = useCallback(() => {
    form.resetFields();
  }, [form]);

  const onFinish = useCallback(
    async (vals) => {
      try {
        await login(vals);
        lStorage.set('gxzrxLogin', true);
        history.push('/main');
      } catch (error) {
        notification.error({
          message: '登陆失败',
          description: error,
        });
      }
    },
    [history]
  );

  return (
    <div className='login-page' style={{ backgroundImage: `url(${bg})` }}>
      <div
        className='login-wrapper'
        // style={{ backgroundImage: `url(${lgbg})`, backgroundSize: '100% 100%' }}
      >
        <span className='title'>雇主责任险管理系统</span>
        <div className='h20'></div>
        <span className='sub-title'>登陆</span>
        <div className='h20'></div>
        <Form onFinish={onFinish} form={form}>
          <Form.Item rules={getrules('请输入用户名')} name='username'>
            <Input prefix={<UserOutlined />} placeholder='请输入用户名' />
          </Form.Item>
          <Form.Item rules={getrules('请输入密码')} name='password'>
            <Input.Password
              prefix={<LockOutlined />}
              placeholder='请输入密码'
            />
          </Form.Item>
          <Row justify='center' gutter={[20]}>
            <Col>
              <Button htmlType='submit' type='primary'>
                登陆
              </Button>
            </Col>
            <Col>
              <Button onClick={onReset}>重置</Button>
            </Col>
          </Row>
        </Form>
      </div>
    </div>
  );
};

export default memo(LoginPage);
