import LoginPage from '../containers/LoginPage';
import Containers from '../containers';
import Base from '../containers/Base';
import OverView from '../containers/OverView';
import Staff from '../containers/Base/Staff';
import Project from '../containers/Base/Project';
import Facility from '../containers/Base/Facility';
import Reimbursement from '../containers/Base/Reimbursement';
import Workbench from '../containers/OverView/Workbench';
import DataFiles from '../containers/OverView/DataFiles';
import FileInfo from '../containers/OverView/FileInfo';

export const getRoutes = (path) => {
  const routes = [
    {
      name: '登陆',
      icon: '',
      auth: [],
      path: '/login',
      target: '_self',
      visible: true,
      component: LoginPage,
    },
    {
      name: '',
      icon: '',
      auth: [],
      path: '/main',
      target: '_self',
      visible: true,
      component: Containers,
      children: [
        {
          name: '概览',
          icon: '',
          auth: [],
          path: '/main/overview',
          target: '_self',
          visible: false,
          component: OverView,
          children: [
            {
              name: '工作台',
              icon: '',
              auth: [],
              path: '/main/overview/workbench',
              target: '_self',
              visible: false,
              component: Workbench,
            },
            {
              name: '数据文件',
              icon: '',
              auth: [],
              path: '/main/overview/data-files',
              target: '_self',
              visible: false,
              component: DataFiles,
            },
            {
              name: '数据文件/文件详情',
              icon: '',
              auth: [],
              path: '/main/overview/file-info',
              target: '_self',
              visible: true,
              component: FileInfo,
            },
          ],
        },
        {
          name: '模型大屏',
          icon: '',
          auth: [],
          path: 'http://38.80.34.16:9999/publish/5ee48d645e26e60f5ef8739a',
          target: '_blank',
          visible: false,
        },
        {
          name: '基础信息',
          icon: '',
          auth: [],
          path: '/main/base',
          target: '_self',
          visible: true,
          component: Base,
          children: [
            {
              name: '员工管理',
              icon: '',
              auth: [],
              path: '/main/base/staff',
              target: '_self',
              visible: false,
              component: Staff,
            },
            {
              name: '项目管理',
              icon: '',
              auth: [],
              path: '/main/base/project',
              target: '_self',
              visible: false,
              component: Project,
            },
            {
              name: '设备管理',
              icon: '',
              auth: [],
              path: '/main/base/facility',
              target: '_self',
              visible: false,
              component: Facility,
            },
            {
              name: '报销管理',
              icon: '',
              auth: [],
              path: '/main/base/reimbursement',
              target: '_self',
              visible: false,
              component: Reimbursement,
            },
          ],
        },
      ],
    },
  ];
  if (path) {
    const _routes = routes.find((route) => route.path === path);
    return _routes.children;
  }
  return routes;
};
