import React, { memo } from 'react';
import { Alert, Row, Col, Select, Table, Button, message, Badge } from 'antd';
import { useFetchDataList, useSearch } from '../../../effects';
import { getfileresultdetail, exportexcel } from '../../../request';
import { getDataResultColumns } from '../../../constants/columns';
import { useCallback } from 'react';
import { downLoadBlob } from '../../../utils';

const Result = (props) => {
  const params = useSearch();
  const { fileId } = params;
  const {
    state,
    onTableChange,
    paginationConfig,
    pagConfig,
    setPagConfig,
  } = useFetchDataList({
    fetchApi: getfileresultdetail,
    defaultPagConfig: {
      batchId: fileId,
      zxfs: undefined,
      zql: undefined,
      ljpdd: undefined,
    },
  });

  const { zxfs, zql, ljpdd, batchId } = pagConfig;

  const setZxfs = useCallback(
    (zxfs) => {
      setPagConfig((config) => ({
        ...config,
        zxfs,
        current: 1,
      }));
    },
    [setPagConfig]
  );
  const setZql = useCallback(
    (zql) => {
      setPagConfig((config) => ({
        ...config,
        zql,
        current: 1,
      }));
    },
    [setPagConfig]
  );
  const setLjpdd = useCallback(
    (ljpdd) => {
      setPagConfig((config) => ({
        ...config,
        ljpdd,
        current: 1,
      }));
    },
    [setPagConfig]
  );

  const exportFile = useCallback(async () => {
    try {
      const data = await exportexcel({
        batchId,
        zxfs,
        zql,
        ljpdd,
      });
      downLoadBlob(data);
    } catch (error) {
      console.log(error);
      message.error('导出失败');
    }
  }, [batchId, zxfs, zql, ljpdd]);
  return (
    <div>
      <Alert showIcon type='info' message='本模型逻辑判断点为8个' />
      <div className='h20'></div>
      <Row gutter={[20]}>
        <Col>
          <Badge status='error' text='高风险' />
        </Col>
        <Col>
          <Badge status='warning' text='中风险' />
        </Col>
        <Col>
          <Badge status='success' text='低风险' />
        </Col>
      </Row>
      <div className='h20'></div>
      <Row gutter={[20]}>
        <Col>
          置信分：
          <Select
            allowClear
            style={{ width: 150 }}
            onChange={setZxfs}
            value={zxfs}
          >
            <Select.Option value={80}>{'>=80分'}</Select.Option>
            <Select.Option value={60}>{'>=60分'}</Select.Option>
            <Select.Option value={40}>{'>=40分'}</Select.Option>
            <Select.Option value={20}>{'>=20分'}</Select.Option>
          </Select>
        </Col>
        <Col>
          逻辑判断点：
          <Select
            allowClear
            style={{ width: 150 }}
            onChange={setLjpdd}
            value={ljpdd}
          >
            <Select.Option value={8}>{'>=8分'}</Select.Option>
            <Select.Option value={7}>{'>=7分'}</Select.Option>
            <Select.Option value={6}>{'>=6分'}</Select.Option>
            <Select.Option value={5}>{'>=5分'}</Select.Option>
            <Select.Option value={4}>{'>=4分'}</Select.Option>
            <Select.Option value={3}>{'>=3分'}</Select.Option>
            <Select.Option value={2}>{'>=2分'}</Select.Option>
          </Select>
        </Col>
        <Col>
          准确率：
          <Select
            allowClear
            style={{ width: 150 }}
            onChange={setZql}
            value={zql}
          >
            <Select.Option value={85}>{'>=85分'}</Select.Option>
            <Select.Option value={60}>{'>=60分'}</Select.Option>
            <Select.Option value={30}>{'>=30分'}</Select.Option>
          </Select>
        </Col>
        <Col>
          <Button type='primary' onClick={exportFile}>
            数据统计分析导出
          </Button>
        </Col>
      </Row>
      <div className='h20'></div>
      <Table
        columns={getDataResultColumns()}
        dataSource={state.list}
        pagination={paginationConfig}
        bordered
        rowKey={(r) => r.id}
        onChange={onTableChange}
        scroll={{ x: 4000, y: 400 }}
      />
    </div>
  );
};

export default memo(Result);
