import React, { memo, useState, useCallback } from 'react';
import { Steps, Row, Col, Button, Divider, message } from 'antd';
import { useMemo } from 'react';
import Statistics from './Statistics';
import OverView from './OverView';
import { downLoadBlob } from '../../../../../utils';
import { exportexcel } from '../../../../../request';

const steps = [
  {
    title: '数据预览',
    component: OverView,
  },
  {
    title: '统计数据',
    component: Statistics,
  },
];

const max = 2;

const Steps_ = (props) => {
  const { onCancel, fileId } = props;

  const [current, setcurrent] = useState(0);

  const _export = useCallback(async (config) => {
    try {
      const data = await exportexcel(config);
      downLoadBlob(data);
    } catch (error) {
      console.log(error);
      message.error('导出失败');
    }
  }, []);

  const onIncrement = useCallback(() => {
    setcurrent((current) => {
      if (current === max) {
        return current;
      }
      return current + 1;
    });
  }, []);

  const onDecrease = useCallback(() => {
    setcurrent((current) => {
      if (current === 0) {
        onCancel({ visible: false });
        return 0;
      }
      // if(current )
      return current - 1;
    });
  }, [onCancel]);

  const Component = useMemo(() => {
    return steps[current].component;
  }, [current]);

  return (
    <div>
      <Steps current={current} size='small'>
        {steps.map((step, index) => {
          return <Steps.Step key={index} title={step.title}></Steps.Step>;
        })}
      </Steps>
      <Divider />
      {Component && (
        <Component
          current={current}
          onIncrement={onIncrement}
          _export={_export}
          fileId={fileId}
          onCancel={onCancel}
        />
      )}
      <Divider />
      <Row justify='end' gutter={[20]}>
        <Col>
          {
            <Button onClick={onDecrease}>
              {current < 1 ? '取消' : '上一步'}
            </Button>
          }
        </Col>
        {/* <Col>
          {current < 1 && (
            <Button type='primary' onClick={onIncrement}>
              导出excel
            </Button>
          )}
        </Col> */}
      </Row>
    </div>
  );
};

export default memo(Steps_);
