import React, { memo, useState, useEffect, useCallback } from 'react';
import { Progress, Alert } from 'antd';
let timer = null;
const LeadInData = (props) => {
  const { current, onCancel, fileId, _setFileId } = props;

  const [percent, setpercent] = useState(0);

  const _setpercent = useCallback(() => {
    setpercent((percent) => {
      if (percent < 100) {
        timer = setTimeout(_setpercent, 100);
        const total = percent + parseInt(Math.random() * 10);
        return total < 100 ? total : 100;
      }
      clearTimeout(timer);
      setTimeout(() => {
        onCancel({ visible: false });
      });
      // setTimeout(() => {});
      // fetchDataList();
      return 100;
    });
  }, [onCancel]);

  const submit = useCallback(async () => {
    // if (current === 2) {

    _setFileId(fileId);
    _setpercent();
    // }
  }, [_setpercent, _setFileId, fileId]);

  useEffect(() => {
    current === 2 && submit();
  }, [submit, current]);
  return (
    <div>
      <Alert
        type={percent === 100 ? 'success' : 'info'}
        message={percent === 100 ? '导入成功' : `正在导入...`}
        showIcon
      />
      <Progress percent={percent} total={100} />
    </div>
  );
};

export default memo(LeadInData);
