import React, { memo, useMemo, useCallback } from 'react';
import { Button, Avatar, Row, Col, Popover } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import './index.styl';
import { useSelector } from 'react-redux';
import { apihost } from '../../constants/host';
const Header = (_) => {
  const userinfo = useSelector((state) => state.userinfo);

  const logOut = useCallback(() => {
    window.location.href = apihost + '/logOff?autoRedirect=true';
  }, []);

  const renderContent = useMemo(() => {
    return (
      <div>
        <Row gutter={[40, 10]} justify='center'>
          <Col>
            <Button onClick={logOut} size='small' type='danger'>
              注销登陆
            </Button>
          </Col>
        </Row>
      </div>
    );
  }, [logOut]);
  return (
    <header className='header-wrapper'>
      <Row
        justify='end'
        align='middle'
        gutter={[10]}
        style={{ height: '100%' }}
      >
        <Col>
          <a href='/excel/czsc.pdf' target='_blank' download='平台操作手册'>
            操作手册下载
          </a>
        </Col>
        <Col>
          <Avatar size='small' icon={<UserOutlined />} />
        </Col>
        <Col>
          <Popover
            placement='bottomRight'
            trigger='hover'
            content={renderContent}
          >
            <span>{userinfo.username || '-'}</span>
          </Popover>
        </Col>
      </Row>
    </header>
  );
};

export default memo(Header);
