import React, { memo, useMemo, useCallback } from 'react';
import { Card, Button } from 'antd';
import { sys_routes } from '../../routes';
import { useGenerateRouting } from '../../effects';
import { RollbackOutlined } from '@ant-design/icons';
import { useHistory } from 'react-router-dom';
const CardTitle = (props) => {
  const { pathArr } = useGenerateRouting();

  const history = useHistory();

  const goBack = useCallback(() => {
    history.goBack();
  }, [history]);

  const generateTitle = useCallback((pathArr, routes = [], name = '') => {
    if (routes.length === 0) return '';
    const length = pathArr.length;
    for (let i = 0; i < pathArr.length; i++) {
      for (let j = i; j < routes.length; j++) {
        if (pathArr[i] === routes[j].path) {
          if (length > 1) {
            pathArr.shift();
            name =
              routes[j].name + '/' + generateTitle(pathArr, routes[j].children);
          } else {
            name += routes[j].name;
          }
          break;
        }
      }
      if (name.startsWith('/')) {
        name = name.substring(1);
      }
      return name;
    }
  }, []);

  const title = useMemo(() => {
    return generateTitle(pathArr, sys_routes);
  }, [generateTitle, pathArr]);
  return (
    <div className='card-wrapper'>
      <Card
        title={title}
        extra={
          <Button onClick={goBack} icon={<RollbackOutlined />}>
            返回
          </Button>
        }
        bordered={false}
      >
        {props.children}
      </Card>
    </div>
  );
};

export default memo(CardTitle);
