import React from 'react';
import { Divider, Typography, Popconfirm } from 'antd';

export const getcolumns = (handleAction) => {
  return [
    { title: '序号', dataIndex: 'batchId' },
    { title: '上传时间', dataIndex: 'createTime' },
    {
      title: '文件名称',
      dataIndex: 'name',
    },
    {
      title: '操作',
      key: 'action',
      render: (record) => (
        <div>
          <a onClick={() => handleAction(0, record)}>查看详情</a>
          <Divider type='vertical' />
          <Popconfirm
            title='确定将此文件删除吗？'
            onConfirm={() => handleAction(1, record)}
          >
            <a>
              <Typography.Text type='danger'>删除</Typography.Text>
            </a>
          </Popconfirm>
        </div>
      ),
    },
  ];
};
