import React, { useMemo, useState, useCallback, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { message } from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import qs from 'querystring';
import { decode } from '../utils';

export function useGenerateRouting() {
  const { pathname } = useLocation();
  const pathArr = useMemo(() => {
    const path = pathname.split('/').filter((i) => i);
    return path.map((_, index) => `/${path.slice(0, index + 1).join('/')}`);
  }, [pathname]);

  return { pathArr };
}

const $defaultPagConfig = {
  current: 1,
  pagesize: 10,
  start: true,
  search: undefined,
};
const $defaultState = {
  list: [],
  count: 0,
};

export function useFetchDataList({
  defaultPagConfig = {},
  defalutState = {},
  fetchApi,
}) {
  const [pagConfig, setPagConfig] = useState({
    ...$defaultPagConfig,
    ...defaultPagConfig,
  });

  const { current, pageSize } = pagConfig;

  const [state, setState] = useState({
    ...$defaultState,
    ...defalutState,
  });

  const fetchDataList = useCallback(
    async (config = {}) => {
      try {
        if (!pagConfig.start) return;
        if (!fetchApi) return;
        const data = await fetchApi({ ...pagConfig, ...config });
        console.log(data);
        setState((state) => ({
          ...state,
          ...data,
        }));
      } catch (error) {
        message.error(`列表获取失败，${error}`);
      }
    },
    [pagConfig, fetchApi]
  );

  const onSearch = useCallback((val) => {
    setPagConfig((config) => ({
      ...config,
      search: val,
      current: 1,
    }));
  }, []);

  const onTableChange = (page, filters, b) => {
    const { current, pageSize } = page;
    const _filters = filters && Object.keys(filters);
    _filters.forEach((filter) => {
      // if()
      filters[filter] && (filters[filter] = filters[filter].join(','));
    });
    setPagConfig((p) => {
      return {
        ...p,
        current,
        pagesize: pageSize,
        ...filters,
      };
    });
  };

  const paginationConfig = {
    current,
    total: state.count,
    pageSize,
    showQuickJumper: true,
    showSizeChanger: true,
    showTotal: (total) => <pre>共{total}条</pre>,
  };

  useEffect(() => {
    fetchDataList();
  }, [fetchDataList]);

  return {
    state,
    paginationConfig,
    onTableChange,
    setPagConfig,
    onSearch,
    fetchDataList,
    pagConfig,
    setState,
  };
}

export function useDispatchAction(action) {
  const dispatch = useDispatch();

  const dispatchAction = useCallback(
    (payload) => {
      dispatch(action(payload));
    },
    [dispatch, action]
  );

  return [dispatchAction];
}

export function useUnitClassify(dep = {}) {
  const { fId: FID = '0', cId: CID = '0' } = dep;
  const unitClassify = useSelector((state) => state.unit.unitClassify);
  const [searchConfig, setSearchConfig] = useState({
    fId: FID,
    cId: CID,
  });
  const { fId, cId } = searchConfig;

  const onFselectChange = useCallback((val) => {
    setSearchConfig((config) => ({
      ...config,
      fId: val,
      cId: val === '0' ? config.cId : '0',
    }));
  }, []);

  const onCselectChange = useCallback((val) => {
    setSearchConfig((config) => ({ ...config, cId: val }));
  }, []);
  const children = useMemo(() => {
    if (fId === '0') {
      let list = [];
      unitClassify.forEach((classify) => {
        classify.children ? (list = list.concat(classify.children)) : void 0;
      });
      return list;
    }
    const data = unitClassify.find((classify) => classify.classId === fId);
    return (data && data.children) || [];
  }, [unitClassify, fId]);

  return { children, fId, cId, onFselectChange, onCselectChange, unitClassify };
}

export function useSearch() {
  const { search } = useLocation();
  const params = useMemo(() => {
    try {
      const paramsString = search.substring(1);
      const p = decode(paramsString);
      return qs.parse(p);
    } catch (error) {
      return false;
    }
  }, [search]);
  return params || {};
}

