import React, { memo, useCallback, useState } from 'react';
import { Divider, Typography, Row, Col, Card, message } from 'antd';
import { getstatisdata } from '../../../../request';
import { useEffect } from 'react';
/**
 *保险统计
 *
 * @param {*} _
 * @returns
 */

const bodyStyle = { textAlign: 'center' };
const InsuranceStatistics = (props) => {
  const { fileId } = props;
  const [state, setstate] = useState(false);

  const fetchData = useCallback(async () => {
    try {
      if (!fileId) return;
      const data = await getstatisdata({ fileid: fileId });
      setstate(data);
    } catch (error) {
      console.log(error);
      message.error(typeof error === 'string' ? error : '系统错误');
    }
  }, [fileId]);

  const _setstate = useCallback(() => {
    if (!fileId) {
      setstate(false);
    }
  }, [fileId]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  useEffect(() => {
    _setstate();
  }, [_setstate]);

  return (
    <div>
      <Typography.Text>保险统计</Typography.Text>
      <div className='h20'></div>
      <Row gutter={[20, 20]}>
        <Col>
          <Card bodyStyle={bodyStyle} title='总保费'>
            <p>{(state && state.zbf) || 0}</p>
          </Card>
        </Col>
        <Col>
          <Card bodyStyle={bodyStyle} title='总理赔金额'>
            <p>{(state && state.zlpje) || 0}</p>
          </Card>
        </Col>
        <Col>
          <Card bodyStyle={bodyStyle} title='总投保人数'>
            <p>{(state && state.ztbrc) || 0}</p>
          </Card>
        </Col>
        <Col>
          <Card bodyStyle={bodyStyle} title='总出险人数'>
            <p>{(state && state.zcxrc) || 0}</p>
          </Card>
        </Col>
      </Row>
      <Divider />
    </div>
  );
};

export default memo(InsuranceStatistics);
