import React, { memo } from 'react';
import { Table } from 'antd';
import { useFetchDataList } from '../../../../../effects';
import { getfiledetail } from '../../../../../request';
import { getDataFileColumns } from '../../../../../constants/columns';

const PreviewData = (props) => {
  const { fileId } = props;
  const { state, paginationConfig, onTableChange } = useFetchDataList({
    defaultPagConfig: {
      fileid: fileId,
      pagesize: 5,
    },
    fetchApi: getfiledetail,
  });
  return (
    <div>
      <Table
        columns={getDataFileColumns()}
        dataSource={state.list}
        scroll={{ x: 4000, y: 300 }}
        pagination={paginationConfig}
        onChange={onTableChange}
        rowKey={(r) => r.id}
      />
    </div>
  );
};

export default memo(PreviewData);
