import React, { memo } from 'react';
import { Switch, Redirect } from 'react-router-dom';
import { getRoutes } from './routes';
import PrivateRoute from './PrivateRoute';

const sys_routes = getRoutes();
const header_routes = getRoutes('/main');

const Routes = memo((props) => {
  const { routes, ...rest } = props;
  return (
    <div className='routes-container' style={{ height: '100%' }}>
      <Switch>
        {routes.map((route) => {
          return (
            <PrivateRoute
              key={route.path}
              path={route.path}
              {...route}
              {...rest}
            />
          );
        })}
        <Redirect from='/' to='/main/overview/workbench' />
      </Switch>
    </div>
  );
});

export { getRoutes, Routes, sys_routes, header_routes };
