import React, { memo, useCallback, useEffect } from 'react';

import { Menu, Image, message } from 'antd';
import { useHistory } from 'react-router-dom';
import { useGenerateRouting } from '../../effects';

import { header_routes } from '../../routes';
import './index.styl';
import { useDispatch } from 'react-redux';
import { getUserInfo } from '../../request';
import { lStorage } from '../../utils';
const SiderBar = (_) => {
  const history = useHistory();

  const dispatch = useDispatch();

  const fetchUserInfo = useCallback(async () => {
    try {
      dispatch(getUserInfo());
    } catch (error) {
      message.error(`个人信息获取失败,${error}`);
    }
  }, [dispatch]);
  useEffect(() => {
    fetchUserInfo();
  }, [fetchUserInfo]);

  const { pathArr } = useGenerateRouting();

  const renderMenus = useCallback((routes) => {
    return routes.map((route) => {
      return !route.visible ? (
        route.children ? (
          <Menu.SubMenu key={route.path} name={route.name} title={route.name}>
            {renderMenus(route.children)}
          </Menu.SubMenu>
        ) : (
          <Menu.Item
            _target={route.target}
            key={route.path}
            name={route.name}
            title={route.name}
          >
            {route.name}
          </Menu.Item>
        )
      ) : null;
    });
  }, []);

  const onSelect = useCallback(
    ({ key }) => {
      if (key.indexOf('http') !== -1) return window.open(key);
      // console.log(menu);
      history.push(key);
    },
    [history]
  );

  return (
    <div className='app-menu'>
      <div className='title'>
        <Image
          src='/imgs/cgzf.svg'
          style={{ marginRight: 10 }}
          width={30}
          height={30}
        />
        雇主责任险管理系统
      </div>
      <Menu
        theme='dark'
        mode='inline'
        onSelect={onSelect}
        selectedKeys={pathArr}
      >
        {renderMenus(header_routes)}
      </Menu>
    </div>
  );
};

export default memo(SiderBar);
