import React, { memo } from 'react';
import { Table } from 'antd';
import { useFetchDataList, useSearch } from '../../../effects';
import { getfiledetail } from '../../../request';
import { getDataFileColumns } from '../../../constants/columns';
const Trace = (props) => {
  const params = useSearch();
  const { fileId } = params;
  const { state, onTableChange, paginationConfig } = useFetchDataList({
    fetchApi: getfiledetail,
    defaultPagConfig: {
      fileid: fileId,
    },
  });
  return (
    <Table
      columns={getDataFileColumns()}
      dataSource={state.list}
      pagination={paginationConfig}
      bordered
      rowKey={(r) => r.id}
      onChange={onTableChange}
      scroll={{ x: 4000, y: 400 }}
    />
  );
};

export default memo(Trace);
