import axios from 'axios';
import store from '../configStore';
import { toggleLoadingModalVisible } from '../actions/action';
import { apihost } from '../constants/host';
import { message } from 'antd';
import { lStorage } from '../utils';
let requestCount = 0;

const instance = axios.create({
  baseURL: apihost,
  withCredentials: true,
  crossDomain: true,
  // timeout: 10000,
});

const pendding = new Map();

const timeOut = new Map();

// 针对未返回的api，避免重复请求进行取消操作

const addPendding = (config) => {
  config.cancelToken = new axios.CancelToken((cancel) => {
    if (!pendding.has(config.identify)) {
      pendding.set(config.identify, cancel);
    }
  });
};

const removePadding = (config) => {
  if (pendding.has(config.identify)) {
    const cancel = pendding.get(config.identify);
    cancel(config.url);
    pendding.delete(config.identify);
  }
};

instance.interceptors.request.use((req) => {
  requestCount++;
  store.dispatch(toggleLoadingModalVisible({ visible: true }));
  removePadding(req);
  addPendding(req);
  return req;
});

instance.interceptors.response.use(
  (resp) => {
    removePadding(resp);
    requestCount--;
    if (requestCount === 0) {
      store.dispatch(toggleLoadingModalVisible({ visible: false }));
    }
    return new Promise((resolve, reject) => {
      if (resp.data.type === 'application/vnd.ms-excel') {
        return resolve(resp.data);
      }
      switch (resp.data.code) {
        case 200:
          if (timeOut.has('timeout')) {
            timeOut.delete('timeout');
          }
          resolve(resp.data.data);
          break;
        case 401:
          lStorage.clear();
          if (!timeOut.has('timeout')) {
            message.info('登陆超时，请重新登陆');
            // window.location.replace('#/login');
          }
          timeOut.set('timeout', true);
          break;
        case 402:
          lStorage.clear();
          if (!timeOut.has('timeout')) {
            message.info('认证失败，请重新登陆');
            // window.location.replace('#/login');
          }
          timeOut.set('timeout', true);
          break;
        default:
          // console.log(resp);
          const err = resp.data.notice || resp.data.msg;
          reject(typeof err === 'string' ? err : '');
          break;
      }
    });
  },
  (err) => {
    requestCount--;
    if (requestCount === 0) {
      store.dispatch(toggleLoadingModalVisible({ visible: false }));
    }
    if (err.message && err.message.includes('timeout')) {
      return Promise.reject('连接超时，请确认网络无误后重试或联系管理员。');
    }
    if (err.message && err.message.includes('Network Error')) {
      return Promise.reject(
        '网络错误，请确认本地网络，服务器网络无误后重试或联系管理员。'
      );
    }
    return Promise.reject(typeof err === 'string' ? err : err && err.message);
  }
);

export default instance;
