import React, { memo, useCallback } from 'react';
import { Modal } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import { toggleModalVisible } from '../../actions/action';

const Modal_ = (props) => {
  const dispatch = useDispatch();

  const modal = useSelector((state) => state.modal.modal);
  const {
    width,
    visible,
    title,
    maskClosable = true,
    height = 'auto',
    component: Component,
    closable = true,
    keyboard = true,
  } = modal;
  const onCancel = useCallback(() => {
    dispatch(toggleModalVisible({ visible: false }));
  }, [dispatch]);
  return (
    <Modal
      closable={closable}
      keyboard={keyboard}
      maskClosable={maskClosable}
      title={title}
      width={width}
      visible={visible}
      onCancel={onCancel}
      footer={null}
      destroyOnClose
    >
      <div>{Component ? Component : null}</div>
    </Modal>
  );
};

export default memo(Modal_);
