import { handleActions } from 'redux-actions';
import {
  toggleRightDrawerVisible,
  toggleLoadingModalVisible,
  toggleModalVisible,
} from '../actions/action';

const initialState = {
  rightDrawer: {
    visible: false,
    component: null,
    width: '50%',
  },
  loadingModal: {
    visible: false,
    tip: '加载中...',
  },
  modal: {
    visible: false,
    component: null,
    width: '80%',
  },
};

const modal = handleActions(
  {
    [toggleModalVisible](state, { payload = {} }) {
      return { ...state, modal: { ...state.modal, ...payload } };
    },
    [toggleRightDrawerVisible](state, { payload = {} }) {
      return { ...state, rightDrawer: { ...state.rightDrawer, ...payload } };
    },
    [toggleLoadingModalVisible](state, { payload = {} }) {
      return { ...state, loadingModal: { ...state, ...payload } };
    },
  },
  initialState
);
export default modal;
