import React, { memo } from 'react';
import { Drawer } from 'antd';
import { useSelector } from 'react-redux';
import { useDispatchAction } from '../../effects';
import { toggleRightDrawerVisible } from '../../actions/action';
const RightDrawer = () => {
  const [$toggleRightDrawerVisible] = useDispatchAction(
    toggleRightDrawerVisible
  );

  const rightDrawer = useSelector((state) => state.modal.rightDrawer);
  const { visible, width, component: Component, title = '盒子' } = rightDrawer;
  return (
    <Drawer
      width={width}
      onClose={() => $toggleRightDrawerVisible({ visible: false })}
      visible={visible}
      title={title}
      destroyOnClose
    >
      <div className='p10'>{Component}</div>
    </Drawer>
  );
};

export default memo(RightDrawer);
