import React, { memo } from 'react';
import { Spin } from 'antd';
import './index.styl';
import { useSelector } from 'react-redux';
const Loading = (props) => {
  const loadingModal = useSelector((state) => state.modal.loadingModal);
  return (
    <div className={`loading ${loadingModal.visible ? 'zindex999' : ''}`}>
      <Spin spinning={loadingModal.visible} tip='加载中...'></Spin>
    </div>
  );
};

export default memo(Loading);
