import axios from './config';
import { setUserInfo } from '../actions/action';
import qs from 'querystring';

export function deletedata(args) {
  return axios.delete(`/deletedata?fileid=${args}`, undefined, {
    identify: 'deletedata',
  });
}

export function getfilelist(args) {
  return axios.get('/getfilelist', { params: args, identify: 'getfilelist' });
}

export function getfiledetail(args) {
  return axios.get('/getfiledetail', {
    params: args,
    identify: 'getfiledetail',
  });
}

export function getfilelistdetail(args) {
  return axios.get('/getfilelistdetail', {
    params: args,
    identify: 'getfilelistdetail',
  });
}

export function getstatisdata(args) {
  return axios.get('/statisdata', { params: args, identify: 'statisdata' });
}

export function login(args) {
  return axios.post('/login', args, { identify: 'login' }).then((res) => {
    // window.loginTimeOut = false;
    return res;
  });
}

export function getUserInfo(args) {
  return async (dispatch) => {
    // window.loginTimeOut = false;
    const data = await axios.get('/getuserinfo', { identify: 'getuserinfo' });
    dispatch(setUserInfo(data));
  };
}

export function exportexcel(args) {
  return axios.get('/exportexcel', {
    params: args,
    responseType: 'blob',
  });
  // return axios.get(`/exportexcel?ids=${args}`);
}

export function getfileresultdetail(args) {
  return axios.get('/getresult', {
    params: args,
    identify: 'getresult',
  });
}
