import React, { memo } from 'react';
import { Row, Col, Input, DatePicker, Table, notification } from 'antd';
import { useFetchDataList } from '../../../effects';
import { getfilelist, deletedata } from '../../../request';
import moment from 'moment';
import { getcolumns } from './columns';
import { useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import { encode } from '../../../utils';
/**
 *数据文件
 *
 * @param {*} _
 * @returns
 */
const DataFiles = (_) => {
  const {
    pagConfig,
    paginationConfig,
    onTableChange,
    state,
    fetchDataList,
    setPagConfig,
  } = useFetchDataList({
    fetchApi: getfilelist,
    defaultPagConfig: {
      time: undefined,
      name: '',
    },
  });

  const history = useHistory();

  const onSearch = useCallback(
    (name) => {
      setPagConfig((config) => ({
        ...config,
        name,
        current: 1,
      }));
    },
    [setPagConfig]
  );

  const onTimeChange = useCallback(
    (time) => {
      setPagConfig((config) => ({
        ...config,
        time,
        current: 1,
      }));
    },
    [setPagConfig]
  );

  const _deleteFile = useCallback(
    async (batchId) => {
      try {
        await deletedata(batchId);
        notification.success({ message: '删除成功' });
        fetchDataList();
      } catch (error) {
        notification.error({ message: '删除失败', description: error });
      }
    },
    [fetchDataList]
  );

  const handleAction = useCallback(
    (key, record) => {
      switch (key) {
        case 0:
          history.push(
            `/main/overview/file-info?${encode(`fileId=${record.batchId}`)}`
          );
          break;
        case 1:
          _deleteFile(record.batchId);
          break;
        default:
          break;
      }
    },
    [history, _deleteFile]
  );

  return (
    <div>
      <Row gutter={[20]} justify='start'>
        <Col>
          <Input.Search onSearch={onSearch} placeholder='请输入文件名称搜索' />
        </Col>
        <Col>
          操作日期：
          <DatePicker onChange={(_, time) => onTimeChange(time)} type='date' />
        </Col>
      </Row>
      <div className='h20'></div>
      <Table
        rowKey={(r) => r.batchId}
        onChange={onTableChange}
        pagination={paginationConfig}
        bordered
        columns={getcolumns(handleAction)}
        dataSource={state.list}
      />
    </div>
  );
};

export default memo(DataFiles);
