import React, { memo, useCallback } from 'react';
import { Typography, Row, Col, Button, Table, message } from 'antd';
import {
  FileAddOutlined,
  FileExclamationFilled,
  DeleteOutlined,
} from '@ant-design/icons';
import { toggleModalVisible } from '../../../../actions/action';
import Step from './Step';
import { useDispatchAction, useFetchDataList } from '../../../../effects';
import { getfiledetail } from '../../../../request';
import DataStatistics from './DataStatistics';
import { getDataFileColumns } from '../../../../constants/columns';
/**
 *所有账单
 *
 * @param {*} _
 * @returns
 */

const scroll = { x: 4000, y: 350 };
const Bills = (props) => {
  const [$toggleModalVisible] = useDispatchAction(toggleModalVisible);

  const { changeFileId } = props;

  const {
    state,
    fetchDataList,
    onTableChange,
    paginationConfig,
    setPagConfig,
    setState,
    pagConfig,
  } = useFetchDataList({
    fetchApi: getfiledetail,
    defaultPagConfig: {
      // time: undefined,
      fileid: '',
      start: false,
    },
  });

  const { fileid } = pagConfig;

  const setFileId = useCallback(
    (fileid) => {
      // setshouldUpdate((count) => count + 1);
      changeFileId(fileid);
      setPagConfig((config) => {
        return {
          ...config,
          fileid,
          start: fileid ? true : false,
        };
      });
    },
    [setPagConfig, changeFileId]
  );

  const _toggleModalVisible = useCallback(
    (opt) => {
      const {
        visible = true,
        keyboard = false,
        closable = false,
        maskClosable = false,
        component: Component = Step,
        fileId,
        title = '数据导入',
      } = opt;

      $toggleModalVisible({
        visible,
        title,
        keyboard,
        closable,
        maskClosable,
        component: (
          <Component
            fileId={fileId}
            onCancel={$toggleModalVisible}
            _setFileId={setFileId}
          />
        ),
      });
    },
    [$toggleModalVisible, setFileId]
  );

  const onStatistical = useCallback(async () => {
    try {
      if (!fileid) return message.error('未上传任何数据');
      _toggleModalVisible({
        component: DataStatistics,
        title: '统计分析',
        keyboard: true,
        closable: true,
        maskClosable: true,
        fileId: fileid,
      });
    } catch (error) {
      message.error('统计失败');
    }
  }, [_toggleModalVisible, fileid]);

  const reset = useCallback(() => {
    if (!fileid) return message.error('未发现数据');
    setState({
      list: [],
      total: 0,
    });

    setFileId('');
    changeFileId('');
  }, [setState, setFileId, fileid, changeFileId]);

  return (
    <div>
      <Row justify='space-between'>
        <Col>
          <Typography.Text>当前数据</Typography.Text>
        </Col>

        <Col>
          <Row gutter={[20]}>
            <Col>
              <Button type='danger' icon={<DeleteOutlined />} onClick={reset}>
                清空当前数据
              </Button>
            </Col>
            <Col>
              <Button
                type='primary'
                onClick={onStatistical}
                icon={<FileExclamationFilled />}
              >
                数据统计分析
              </Button>
            </Col>
            <Col>
              <Button
                type='primary'
                onClick={_toggleModalVisible}
                icon={<FileAddOutlined />}
              >
                导入数据
              </Button>
            </Col>
            {/* <Col>
              <Button
                // onClick={_toggleModalVisible}
                icon={<FileExcelOutlined />}
              >
                <a href='/excel/sjdc.xls' download='数据统计'>
                  导出数据
                </a>
              </Button>
            </Col> */}
          </Row>
        </Col>
      </Row>
      {/* <Row>
        <Col>
          <DatePicker
            // defaultValue={moment(time)}
            onChange={(_, time) => onTimeChange(time)}
            type='date'
          />
        </Col>
      </Row> */}
      <div className='h20'></div>
      <Table
        columns={getDataFileColumns()}
        scroll={scroll}
        // rowSelection={rowSelection}
        dataSource={state.list}
        onChange={onTableChange}
        pagination={paginationConfig}
        bordered
        rowKey={(r) => r.id}
      />
    </div>
  );
};

export default memo(Bills);
