import React, { memo, useCallback } from 'react';
import { Table, Alert, Row, Col, Select, Button, Badge } from 'antd';
import { useFetchDataList } from '../../../../../effects';
import { getfileresultdetail } from '../../../../../request';
import { generateUUID } from '../../../../../utils';
import { getDataResultColumns } from '../../../../../constants/columns';
import { useEffect } from 'react';

const Overview = (props) => {
  const { fileId, _export, onIncrement } = props;
  const {
    state,
    paginationConfig,
    onTableChange,
    pagConfig,
    setPagConfig,
  } = useFetchDataList({
    fetchApi: getfileresultdetail,
    defaultPagConfig: {
      batchId: fileId,
    },
  });

  const { zxfs, zql, ljpdd, batchId } = pagConfig;

  const $export = useCallback(() => {
    if (batchId) {
      _export({ zxfs, zql, ljpdd, batchId });
      onIncrement();
    }
  }, [zxfs, zql, ljpdd, batchId, _export, onIncrement]);

  const setZxfs = useCallback(
    (zxfs) => {
      setPagConfig((config) => ({
        ...config,
        zxfs,
        current: 1,
      }));
    },
    [setPagConfig]
  );
  const setZql = useCallback(
    (zql) => {
      setPagConfig((config) => ({
        ...config,
        zql,
        current: 1,
      }));
    },
    [setPagConfig]
  );
  const setLjpdd = useCallback(
    (ljpdd) => {
      setPagConfig((config) => ({
        ...config,
        ljpdd,
        current: 1,
      }));
    },
    [setPagConfig]
  );

  return (
    <div>
      <Alert showIcon type='info' message='本模型逻辑判断点为8个' />
      <div className='h20'></div>
      <Row gutter={[20]}>
        <Col>
          <Badge status='error' text='高风险' />
        </Col>
        <Col>
          <Badge status='warning' text='中风险' />
        </Col>
        <Col>
          <Badge status='success' text='低风险' />
        </Col>
      </Row>
      <div className='h20'></div>
      <Row gutter={[20]}>
        <Col>
          置信分：
          <Select
            allowClear
            style={{ width: 150 }}
            onChange={setZxfs}
            value={zxfs}
          >
            <Select.Option value={80}>{'>=80分'}</Select.Option>
            <Select.Option value={60}>{'>=60分'}</Select.Option>
            <Select.Option value={40}>{'>=40分'}</Select.Option>
            <Select.Option value={20}>{'>=20分'}</Select.Option>
          </Select>
        </Col>
        <Col>
          逻辑判断点：
          <Select
            allowClear
            style={{ width: 150 }}
            onChange={setLjpdd}
            value={ljpdd}
          >
            <Select.Option value={8}>{'>=8分'}</Select.Option>
            <Select.Option value={7}>{'>=7分'}</Select.Option>
            <Select.Option value={6}>{'>=6分'}</Select.Option>
            <Select.Option value={5}>{'>=5分'}</Select.Option>
            <Select.Option value={4}>{'>=4分'}</Select.Option>
            <Select.Option value={3}>{'>=3分'}</Select.Option>
            <Select.Option value={2}>{'>=2分'}</Select.Option>
          </Select>
        </Col>
        <Col>
          准确率：
          <Select
            allowClear
            style={{ width: 150 }}
            onChange={setZql}
            value={zql}
          >
            <Select.Option value={85}>{'>=85分'}</Select.Option>
            <Select.Option value={60}>{'>=60分'}</Select.Option>
            <Select.Option value={30}>{'>=30分'}</Select.Option>
          </Select>
        </Col>
        <Col>
          <Button type='primary' onClick={$export}>
            数据统计分析导出
          </Button>
        </Col>
      </Row>
      <div className='h20'></div>
      <Table
        dataSource={state.list}
        bordered
        columns={getDataResultColumns()}
        pagination={paginationConfig}
        rowKey={(r) => r.id}
        scroll={{ x: 4000, y: 300 }}
        onChange={onTableChange}
        r={() => generateUUID()}
      />
    </div>
  );
};

export default memo(Overview);
