import React, { memo, useState, useCallback, useEffect } from 'react';
import { Steps, Row, Col, Button, Divider, notification } from 'antd';
import LeadInFile from '../LeadInFile';
import { useMemo } from 'react';
import PreviewData from '../PreviewData';
import LeadInData from '../LeadInData';
import { deletedata } from '../../../../../request';
import { useHistory } from 'react-router-dom';
import { lStorage } from '../../../../../utils';

const steps = [
  {
    title: '上传文件',
    component: LeadInFile,
  },
  {
    title: '数据预览',
    component: PreviewData,
  },
  {
    title: '数据导入',
    component: LeadInData,
  },
];

const max = 2;

const Steps_ = (props) => {
  const { onCancel, _setFileId } = props;

  const history = useHistory();

  const [fileId, setFileId] = useState();
  const [current, setcurrent] = useState(0);
  // console.log('step');
  const onIncrement = useCallback(() => {
    setcurrent((current) => {
      if (current === max) {
        return current;
      }
      return current + 1;
    });
  }, []);

  const onDecrease = useCallback(async () => {
    try {
      if (fileId) {
        await deletedata(fileId);
        notification.success({ message: '已取消' });
      }

      onCancel && onCancel({ visible: false });
    } catch (error) {
      notification.error({ message: '系统错误' });
    }
  }, [onCancel, fileId]);

  const onFileChange = useCallback(
    ({ file }) => {
      if (file.response && file.response.code === 402) {
        lStorage.clear();
        history.push('/login');
      }
      if (file.response && file.response.data) {
        setFileId(file.response.data);
      }
    },
    [history]
  );

  const Component = useMemo(() => {
    return steps[current].component;
  }, [current]);

  return (
    <div>
      <Steps current={current} size='small'>
        {steps.map((step, index) => {
          return <Steps.Step key={index} title={step.title}></Steps.Step>;
        })}
      </Steps>
      <Divider />
      {Component && (
        <Component
          current={current}
          onFileChange={onFileChange}
          // fetchDataList={fetchDataList}
          fileId={fileId}
          _setFileId={_setFileId}
          onCancel={onCancel}
        />
      )}
      <Divider />
      <Row justify='end' gutter={[20]}>
        <Col>{current < 2 && <Button onClick={onDecrease}>取消</Button>}</Col>
        <Col>
          {current < 2 && (
            <Button disabled={!fileId} type='primary' onClick={onIncrement}>
              {current === 1 ? '导入' : '下一步'}
            </Button>
          )}
        </Col>
      </Row>
    </div>
  );
};

export default memo(Steps_);
